<template>
  <div id="app">
    
    <img class="game-logo" src="./assets/hos_logo_m01_sm.png">
    <h1>Welcome to Hots gayds!</h1>
<section class="filter">
  <h2>Filter</h2>
  <div class="filters">

    <div class="filter-universe">
      <caption>Universe</caption>
      <label for="universe">Diablo</label>
      <input class="option-input" type="checkbox" name="Diablo">
      <label for="universe">StarCraft</label>
      <input class="option-input" type="checkbox" name="StarCraft">
      <label for="universe">Warcraft</label>
      <input class="option-input" type="checkbox" name="Warcraft">
    </div>

    <div class="filter-roles">
      <caption>Role</caption>
      <label for="classes">Warrior</label>
      <input class="option-input" type="checkbox" name="warrior">
      <label for="classes">Support</label>
      <input class="option-input" type="checkbox" name="support">
      <label for="classes">Assasin</label>
      <input class="option-input" type="checkbox" name="assasin">
      <label for="classes">Specialist</label>
      <input class="option-input" type="checkbox" name="specialist">             
    </div>

    <div class="filter-name">
      <caption>Search Name</caption>
      <input type="text" placeholder="search">
    </div>

  </div>  
</section>

<div class="cont-adv"></div>

<section class="all-heroes">

  <div class="heroes">

    <div class="hero" v-for="(hero, index) in hotsdb">
      <router-link :to="{ name: 'hero', params: { id: index } }">
      <img :src="hero.img" alt="">
      <div class="name">{{ hero.name }}</div>
      <div class="role">{{ hero.role }}</div>
      </router-link>
    </div>

  </div>


</section>

<div class="cont-adv"></div>

  </div>


</template>

<script>

import hotsdb from '../src/db/hotsdb.js'

export default {
  name: 'app',
  data () {
    return {
      hotsdb: hotsdb
    }
  }
}

</script>





<style lang="scss">
$color: #fff;
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: $color;
  margin-top: 15px;
}

#app .game-logo {
  width: 100%;
  max-width: 1100px;
}

.filter {
  margin: 0px 35px;
}

.filter h2 {
  text-align: left;
}


.filters {
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-flex-direction: row;
  -ms-flex-direction: row;
  flex-direction: row;
}


.filters caption {
  font-size: 22px;
}

.filter-universe, .filter-roles, .filter-name {
  display: block;
  width: 150px;
  text-align: left;  
}


</style>



