<template>
  <div id="app">
    
    <img class="game-logo" src="./assets/hos_logo_m01_sm.png">
    <h1>Welcome to Hots gayds!</h1>

<div class="cont-adv"></div>

  <div class="hero" v-if="hero">
    <img :src="hero.img" alt="">
    <h2>{{ hero.name }}</h2>
    <p>{{ hero.role }}</p>
  </div>



<div class="cont-adv"></div>

  </div>


</template>

<script>

import hotsdb from '../src/db/hotsdb.js'

export default {
  name: 'app',
  data () {
    return {
      hotsdb: hotsdb,
        hero: null
    }
  },
  created: function() {
    var heroId = this.$route.params.id
    this.hero = this.hotsdb[heroId]
  }
}

</script>





<style lang="scss">
$color: #fff;
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: $color;
  margin-top: 15px;
}

#app .game-logo {
  width: 100%;
  max-width: 1100px;
}


</style>



